import React, { useRef, useEffect, useState } from "react"
import styled from "styled-components"
import { Sidebar } from "./sidebar"
import { SEO } from "./seo"
import { Layout } from "./layout"
import Logo from "./../components/logo"
import { Sizes, Device } from "../global/sizes"
import ReactGA from "react-ga"
export const PageWithoutRef = ({ children, style }, pageRef) => {
    useEffect(() => {
        ReactGA.initialize("UA-96249022-1")
        ReactGA.pageview(window.location.pathname + window.location.search)
    }, [])

    return (
        <Layout>
            <SEO title="Home" />
            <Sidebar />
            <Logo size={170} />
            <StyledPage
                id="container"
                ref={pageRef}
                onWheel={e => scroll(e, pageRef)}
                style={{ ...style }}
            >
                {children}
            </StyledPage>
        </Layout>
    )
}

export const Page = React.forwardRef(PageWithoutRef)

const scroll = (e, ref) => {
    if (window.innerWidth > parseInt(Sizes.laptop)) {
        if (!ref || !ref.current) return
        var containerScrollPosition = ref.current.scrollLeft
        const delta =
            Math.abs(e.deltaY) > Math.abs(e.deltaX) ? e.deltaY : e.deltaX
        ref.current.scrollTo({
            top: 0,
            left: containerScrollPosition + delta,
            behaviour: "smooth",
        })
    }
}

const StyledPage = styled.div`
    background-color: #dbc0af;
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    box-sizing: border-box;
    flex-direction: column;
    -webkit-overflow-scrolling: touch;
    -ms-touch-action: none;
    touch-action: none;
    height: calc(100vh + 40px);
    overflow: scroll;
    flex-wrap: nowrap;
    margin-left: 0;

    @media ${Device.laptop} {
        flex-direction: row;
        margin-left: 80px;
        padding-left: 200px;
    }
`
