import React from "react"
import styled, { css } from "styled-components"

function HeadingWithoutRef({ children, level = "1", lined, decorated }, ref) {
    let newLevel = level

    if (!level) newLevel = 1

    if (decorated)
        return (
            <Decorated>
                <StyledHeading lined={lined} level={newLevel} ref={ref}>
                    {children}
                </StyledHeading>
            </Decorated>
        )
    return (
        <StyledHeading lined={lined} level={newLevel} ref={ref}>
            {children}
        </StyledHeading>
    )
}

export const Heading = React.forwardRef(HeadingWithoutRef)

const Decorated = styled.div`
    position: relative;
    display: inline-block;
    z-index: 2;

    &:before {
        display: block;
        position: absolute;
        bottom: 20px;
        left: -5px;
        right: -3px;
        width: auto;
        height: 5px;
        background-color: #e8d7cc;
        content: "";
        z-index: 1;
    }

    &:after {
        display: block;
        position: absolute;
        top: 7px;
        right: -15px;
        width: 5px;
        height: 5px;
        border-bottom: 2px solid #000;
        border-right: 2px solid #000;
        content: "";
        transform: rotate(-45deg);
        z-index: 1;
    }
`

const Lined = styled.div`
    display: block;
    margin-left: 70px;
    position: relative;
`

const StyledHeading = styled.h1`
    font-family: "Butler";
    position: relative;
    z-index: 10;

    ${p =>
        p.lined &&
        css`
            &:before {
                content: "";
                float: left;
                width: 1px;
                height: 50px;
                clear: both;
            }
            &:after {
                content: "";
                float: left;
                clear: both;
                height: 1px;
                width: 100px;
                background-color: #000;
                margin: 20px 20px 0 -60px;
            }
        `}

    ${p =>
        p.level == "1" &&
        css`
            font-size: 42px;
            line-height: 52px;
            margin-bottom: 20px;
        `}

    ${p =>
        p.level == "2" &&
        css`
            font-size: 25px;
            line-height: 26px;
            letter-spacing: 0.75px;
            font-weight: 600;
        `}
`
