import React from "react"
import styled from "styled-components"

function ParagraphWithoutRef({ children }, ref) {
    return <StyledParagraph ref={ref}>{children}</StyledParagraph>
}

export const Paragraph = React.forwardRef(ParagraphWithoutRef)

const StyledParagraph = styled.p`
    font-family: "Avenir";
    font-size: 17px;
    line-height: 28px;
    margin-bottom: 20px;
`
